.nav-wrapper
{
    background: rgb(0,0,0,0.1);
    padding: 0.2rem 0;
    position: sticky;
    top: 0;
    z-index: 30;
    backdrop-filter: blur(50px);
    
}

.nav-content
{
    max-width: 1300px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 1rem 0;
    margin: 0 auto;
}
.nav-content > *:last-child {
    margin-right: 10px;
}
.nav-content ul
{
    display: flex;
    align-items: center;
    gap: 0.5rem;
    list-style: none;
}
.nav-content li{
    margin: 0 1.5rem;
}

.menu-item {
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: 500;
    color: #fff;
    position: relative; /* Required for absolute positioning of ::before */
    cursor: pointer;
    display: inline-block; /* Ensures the element can properly contain the pseudo-element */
}

.menu-item::before {
    content: " ";
    position: absolute;
    width: 0; 
    height: 2px; 
    bottom: -5px; 
    left: 50%;
    background: linear-gradient(90deg, #a993fe 0%, #51cae3 100%);
    border-radius: 1rem;
    transition: all 0.3s ease;
    transform: translateX(-50%); 
    box-shadow: 0 0 8px #7e61e7; 
    opacity: 0; 
}

.menu-item:hover::before {
    width: 100%; 
    opacity: 1; 
    left: 0; 
    transform: translateX(0%);
}

.contact-btn
{
    font-size: 0.9rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: linear-gradient(90deg,#a993fe 0%,#51cae3 100%);
    padding: 0.3rem 1rem;
    border: 1px solid #51cae3;
    outline: 1.5px solid transparent;
    border-radius: 0.2rem;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-right: 6px;
}

.contact-btn:hover
{
    color: #51cae3;
    background: #000;
    outline: 1.5px solid transparent;
    border: 1px solid #fff;
}

.menu-btn
{
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    border: none;
    border-radius: 0.4rem;
    color: #fff;
    background: linear-gradient(90deg,#a993fe -0.13%,#7e61e7 99.87%);
    line-height: 0;
    cursor: pointer;
    transition: all 0.4s ease;
    display:none;
}
.menu-btn:hover{
    color: #a993fe;
    background: #000;
    border: 1px solid #a993fe;
}

@media(max-width :769px)
{
    .menu-btn
    {
        display: block;
    }


.nav-content ul{
    display: none;
}
}

@media(max-width:1325px)
{
    .nav-wrapper
    {
        padding: 0 2rem;
    }
}